import React, { useEffect, useState } from 'react'
import { Label } from '../../commanComponent'
import CustomSelect from '../../commanComponent/CustomSelect'
import { useDispatch, useSelector } from 'react-redux'
import { useCustomTranslation } from '../../../translation'
import { payrollActions } from '../../../core/resource/actions'
import CreateUpdateSalaryTemplate from '../salaryTemplate/CreateUpdateSalaryTemplate'
import BackIcon from '../../commanComponent/BackIcon'
import { Link, } from 'react-router-dom'

const AddUpdateEmpSalary = () => {

    const { t } = useCustomTranslation()
    const dispatch = useDispatch()
    const [allSalaryTemplates, setAllSalaryTemplates] = useState('')
    const [salaryTempFormData, setSalaryTempFormData] = useState({
        salaryTemp: null
    })
    const {
        getAllSalaryTemplatePending,
        getAllSalaryTemplateRes,
        getAllSalaryTemplateError,

        selectedEmployeeData
    } = useSelector(state => state.payroll)

    useEffect(() => {
        if (!getAllSalaryTemplatePending) {
            if (getAllSalaryTemplateError) {
                return;
            }
            if (getAllSalaryTemplateRes) {
                if (getAllSalaryTemplateRes?.code === 200) {
                    setAllSalaryTemplates(getAllSalaryTemplateRes?.data)
                }
            }
        }
    }, [
        getAllSalaryTemplateRes,
        getAllSalaryTemplateError,
        getAllSalaryTemplatePending
    ])
    const getAllSalaryTemplateData = () => {
        dispatch(payrollActions.getAllSalaryTemplateRequest())
    }
    useEffect(() => {
        getAllSalaryTemplateData()
    }, [])
    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, } = e?.target ? e?.target : { value: e };
        setSalaryTempFormData(prevState => {
            let updatedState = {
                ...prevState,
                [name]: value,
            };
            if ("salaryTemp" === name) {
                dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_GET_SALARY_TEMPLATE_BY_ID_STORE"))
            }
            return updatedState;
        });
    };

    return (<>

        <div className="formPanel cardBox mb-3">
            <div className='d-flex mb-3'>
                <div>
                    <Link to={"/payroll-employees"}>
                    <BackIcon />
                    </Link>
                </div>
                <div>
                    <h5 >
                        {selectedEmployeeData?.name}
                    </h5>
                </div>
                <div className='ms-2 mt-1'>
                    <span className='span-label'>Emp.ID:{selectedEmployeeData?.emp_id}</span>
                </div>
            </div>
            <div className="row">
                <div className="col-md-4">
                    <div className="form-group mb-2">
                        <Label text={t("labels.salaryTemplate")} />
                        <CustomSelect
                            data={allSalaryTemplates}
                            placeholder={t("placeholders.selectSalaryTemplate")}
                            value={salaryTempFormData?.salaryTemp || ''}
                            handleChange={(selectedOption) => handleFromChange(selectedOption, "salaryTemp")}
                            name="salaryTemp"
                        />
                    </div>
                </div>
            </div>
        </div>
        {salaryTempFormData?.salaryTemp &&
            <CreateUpdateSalaryTemplate
                selectedSalaryTemplate={salaryTempFormData?.salaryTemp}
                employeeID={selectedEmployeeData?._id}
            />
        }
    </>
    )
}

export default AddUpdateEmpSalary
